/** Motif repeats on Overview page */

import {
  Chart,
  Motif,
  MotifColor,
  getChartFromText,
  getFlattenRow,
  getMergedChart,
  getRightLeftFlippedChart,
  getVerticalStackedChart,
} from "@tanyoknits/shared";
import { cable, twistLace } from "./stitches";
import { sizes } from "./sizes";
import { getMotifBySize } from "../../utils/misc";

const gutters = sizes.map((_) => getChartFromText(["p k:2 p"]));
const halfGutters = sizes.map((_) => getChartFromText(["k p"]));

const motifColor: MotifColor[] = ["orange", "teal"];

export const repeatedPattern: Chart[] = sizes.map((size, i) => {
  const cableMotif = { ...getMotifBySize(cable, size), id: motifColor[0] };
  const cablePadding: Motif = {
    chart: getChartFromText(["k:4"]),
    stsCount: 4,
    id: motifColor[0],
  };
  const laceMotifBySize = getMotifBySize(twistLace, size);
  const flattedLaceChart = laceMotifBySize.chart.map((row) =>
    getFlattenRow(row)
  );
  const laceMotif = {
    ...getMotifBySize(twistLace, size),
    chart: flattedLaceChart,
    id: motifColor[1],
  };
  const lacePadding: Motif = {
    chart: getChartFromText(["p k2tog yo p", "k:4"]).map((row) =>
      getFlattenRow(row)
    ),
    stsCount: 4,
    id: motifColor[1],
  };

  const chart1 = getVerticalStackedChart(
    [
      cableMotif,
      cableMotif,
      cablePadding,
      cablePadding,
      laceMotif,
      laceMotif,
      lacePadding,
    ],
    true
  );

  const chart2 = getVerticalStackedChart(
    [
      laceMotif,
      laceMotif,
      lacePadding,
      cableMotif,
      cableMotif,
      cablePadding,
      cablePadding,
    ],
    true
  );

  return getMergedChart(
    [
      halfGutters[i],
      chart1,
      gutters[i],
      chart2,
      getRightLeftFlippedChart(halfGutters[i]),
    ],
    20
  );
});
