import {
  BasePattern,
  definition,
  getMergedBasePattern,
  spliceRow,
} from "@tanyoknits/shared";

const { koco } = definition;

export function getBodyStartBasePattern(
  frontLeft: BasePattern,
  back: BasePattern,
  frontRight: BasePattern,
  underarm: number
): BasePattern {
  const coRow = back.baseChart[0];
  spliceRow(coRow, 0, underarm, koco);
  spliceRow(coRow, null, underarm, koco);
  return getMergedBasePattern([frontLeft, back, frontRight]);
}
