import {
  MotifColor,
  MotifComponent,
  PlainTextList,
  SectionInstruction,
  getBasePatternFromChart,
  getChartFromBasePattern,
  getEven,
  getMergedBasePattern,
  getMotifFilledChart,
  getRound,
  getSlicedBasePattern,
} from "@tanyoknits/shared";
import PageContentWrapper from "../../layout/PageContentWrapper";
import { PatternBySizeProps } from "../../layout/PatternBySize";
import { repeatedPattern } from "./motifs";
import { schemaBySize } from "./sizes";
import {
  getVNeckYokeBasePatternByParts,
  vNeckRaglanCardiganOverview as overview,
} from "../../patternMakers/raglan/cardiganVNeck";
import InteractivePattern from "../../components/InteractivePattern";
import { getNeedleText, needles } from "../../utils/needles";
import { getBodyStartBasePattern } from "../../patternMakers/raglan/cardiganCommon";
import { getSleeveBasePattern } from "../../patternMakers/common/sleeves";

export default function Instruction({ menu, size }: PatternBySizeProps) {
  const repeat = repeatedPattern[size];
  const schema = schemaBySize[size];
  const { co, start, rows, yokeSplit, total, vNeck, arm } = schema;
  const { every4RowAfterRaglan } = vNeck;
  const { hemDecStart, lastNeckInc, split, bo } = rows;
  const { body, sleeve } = total;
  const { frontOneSide, underarm } = yokeSplit;
  const { cuffDecRow, cuffBO } = arm;

  // Full body part including all increase sts till hem decrease - excluding sleeves
  const fullBodyChart = getMotifFilledChart(repeat, body, hemDecStart, 3);
  const motifRepeatRow = repeat.length;
  const combinedMotif = {
    id: "grape" as MotifColor,
    stsCount: 4,
    rowCount: motifRepeatRow,
  };
  const fullBodyBasePattern = getBasePatternFromChart(
    fullBodyChart,
    null,
    0,
    combinedMotif
  );
  // full body parts
  const frontLeftBasePattern = getSlicedBasePattern(
    fullBodyBasePattern,
    [0, lastNeckInc],
    [0, frontOneSide + every4RowAfterRaglan]
  );
  const frontRightBasePattern = getSlicedBasePattern(
    fullBodyBasePattern,
    [0, lastNeckInc],
    [body - (frontOneSide + every4RowAfterRaglan), body]
  );
  const backBasePattern = getSlicedBasePattern(
    fullBodyBasePattern,
    [0, split],
    [
      frontOneSide + every4RowAfterRaglan + underarm,
      body - (frontOneSide + every4RowAfterRaglan + underarm),
    ]
  );

  // Sleeve - include yoke & after split
  const fullSleeveChart = getMotifFilledChart(
    repeat,
    sleeve,
    split + cuffDecRow,
    3
  );
  const sleeveBasePattern = getBasePatternFromChart(
    fullSleeveChart,
    null,
    0,
    combinedMotif
  );
  const sleeveYokeBasePattern = getSlicedBasePattern(
    sleeveBasePattern,
    [0, split],
    [underarm / 2, sleeve - underarm / 2]
  );

  /** pattern parts */

  // Total yoke base patterns
  const yokeBasePatterns = getVNeckYokeBasePatternByParts(
    frontLeftBasePattern,
    frontRightBasePattern,
    backBasePattern,
    sleeveYokeBasePattern,
    schema
  );
  // Yoke
  const yorkPartBasePattern = getMergedBasePattern(
    yokeBasePatterns,
    split - 1,
    ["A", "B", "C", "D"]
  );

  // Total body
  const frontLeftInc = getSlicedBasePattern(
    yokeBasePatterns[0],
    [split - 1, lastNeckInc],
    [0, null]
  );
  const backWithUnderArm = getSlicedBasePattern(
    fullBodyBasePattern,
    [split - 1, lastNeckInc],
    [
      frontOneSide + every4RowAfterRaglan,
      body - (frontOneSide + every4RowAfterRaglan),
    ]
  );
  const frontRightInc = getSlicedBasePattern(
    yokeBasePatterns[yokeBasePatterns.length - 1],
    [split - 1, lastNeckInc],
    [0, null]
  );
  const bodyStartBasePattern = getBodyStartBasePattern(
    frontLeftInc,
    backWithUnderArm,
    frontRightInc,
    underarm
  );

  // body hem
  const hemDec = getEven(body * 0.1);
  const boSts = body - hemDec * 2;

  // sleeve pattern
  const sleeveFromUnderarmBasePattern = getSleeveBasePattern(
    sleeveBasePattern,
    schema
  );

  // overview
  const { sleeve: startS, back: startB, frontOneSide: startF } = start;
  const initPMs = [
    `PM twice at ${startF} sts from each end to indicate the divide between front and sleeve`,
    `PM twice at ${startS} sts from the previous markers to indicate the divide between sleeve and back`,
    `From the start, let's call marker A, B, C, D; back is between marker B and C and it has ${startB} sts`,
    `Work two rows, then work the following chart for the yoke`,
  ];

  return (
    <PageContentWrapper>
      {menu === 0 && (
        <>
          <PlainTextList list={overview} />
          <MotifComponent chart={repeat} stsCount={20} />
        </>
      )}
      {menu === 1 && (
        <InteractivePattern
          chart={{
            chart: getChartFromBasePattern(yorkPartBasePattern),
            size: "S",
            direction: "flat",
            showStsCountPerRow: true,
            stsMarkers: yorkPartBasePattern.stsMarkers,
          }}
          instruction={{
            co: {
              needle: getNeedleText(needles[6]),
              coCount: co,
              direction: "flat",
            },
            beforeList: initPMs,
          }}
        />
      )}
      {menu === 2 && (
        <>
          <SectionInstruction
            title="Body & sleeves split"
            rowType={`Rnd ${split} + CO`}
            rows={[
              {
                text: `Work till mA, slip sts in scrap yarn or stitch holder till mB, CO ${underarm} with KOCO method, work till mC, slip sleeve sts till mD, CO ${underarm}, work till end`,
                notes: `total ${body - every4RowAfterRaglan * 2} sts`,
              },
            ]}
            afterList={[
              "Follow by final V neck increase as in the chart below",
              `Work with the stitch pattern until row ${hemDecStart} or a desired length before hem`,
            ]}
          />
          <InteractivePattern
            chart={{
              chart: getChartFromBasePattern(bodyStartBasePattern),
              size: "S",
              direction: "flat",
              showStsCountPerRow: true,
              startRowID: split - 1,
            }}
          />
          <SectionInstruction
            title="Waistline dec"
            rowType={`Rnd ${hemDecStart}`}
            rows={[
              {
                text: `Dec evenly about 10%, ${hemDec} sts`,
                notes: `total ${boSts} sts`,
              },
            ]}
          />
          <SectionInstruction
            title="Hem ribbing"
            rowType={`Rnd ${hemDecStart + 1}`}
            rows={[
              {
                text: `(k1, p1), rep (-) to end (${boSts / 2} times)`,
              },
              { text: "as rnd 1" },
            ]}
            notes={`Work in ribbing till row ${bo}. BO with Sewn-on BO is recommended.`}
          />
        </>
      )}
      {menu === 3 && (
        <>
          <SectionInstruction
            intro="ðŸª¡ Move the sts in scrap yarn or stitch holder the larger circular needle."
            rowType="Pick up"
            rows={[
              {
                text: `underarm ${underarm} sts`,
                notes: `sleeve total ${sleeve} sts`,
              },
            ]}
            afterList={[
              `Pm after ${getRound(
                underarm / 2
              )} sts - this is BOR of sleeve. Rnd 1 start here.`,
              `Work about ${cuffDecRow} rows before cuff.`,
              `Work sleeve tapering as in the chart below, and dec evenly about before cuff ribs`,
            ]}
          />
          <InteractivePattern
            chart={{
              chart: getChartFromBasePattern(sleeveFromUnderarmBasePattern),
              size: "S",
              direction: "round",
              showStsCountPerRow: true,
              startRowID: split - 1,
            }}
          />
          <SectionInstruction
            title="Hem 1x1 ribbing"
            rowType="Rnd"
            startRowID={cuffDecRow + 1}
            rows={[{ text: `(k1, p1) repeat (-) till BOR` }]}
            notes={`Rep the prev row to about ${
              cuffBO - cuffDecRow
            } rows before cuff BO. Sewn-on BO is recommended.`}
          />
        </>
      )}
    </PageContentWrapper>
  );
}
