import { Flex, Text, VStack } from "@chakra-ui/react";
import { MotifBySizeItem } from "../utils/types";
import { useState } from "react";
import {
  MotifComponent,
  StitchList,
  getUniqueStsFromChart,
} from "@tanyoknits/shared";
import SelectSize from "./SelectSize";
import InteractiveAlert from "./InteractiveAlert";
interface Props {
  intro?: string;
  motifBySize: MotifBySizeItem[];
  showReflected?: boolean;
}
export default function StitchPattern({
  intro,
  motifBySize,
  showReflected = false,
}: Props) {
  const [selectedSize, setSelectedSize] = useState<number>(0);
  const { motif, direction } = motifBySize[selectedSize] ?? motifBySize[0];
  const stitches = getUniqueStsFromChart(motif.chart);

  return (
    <Flex direction="column" gap={6}>
      {intro != null && <Text>{intro}</Text>}
      <VStack spacing={6} align="left">
        <Flex gap={6} alignItems="center" flexWrap="wrap">
          {motifBySize.length > 1 && (
            <SelectSize
              sizes={motifBySize.map((m) => m.sizes.join(", "))}
              selectedSize={selectedSize}
              onChange={(id: number) => setSelectedSize(id)}
            />
          )}
          <InteractiveAlert />
        </Flex>
        <MotifComponent {...motif} direction={direction} />
        <StitchList stitches={stitches} hasWS={true} />
      </VStack>
    </Flex>
  );
}
