import { Box, Flex } from "@chakra-ui/react";
import { useState } from "react";
import DisplayOptions from "./DisplayOptions";
import {
  ChartComponent,
  ChartProps,
  FocusType,
  PlainTextList,
  Written,
} from "@tanyoknits/shared";
import { PatternPartInstruction } from "../utils/types";
import COInstruction from "./COInstruction";
import InteractiveAlert from "./InteractiveAlert";

interface Props {
  chart: ChartProps;
  instruction?: PatternPartInstruction;
}

export default function InteractivePattern({ chart, instruction }: Props) {
  const [menu, setMenu] = useState<string>("1");
  const [focus, setFocus] = useState<FocusType>([0, null]);

  return (
    <Flex direction="column" gap={4}>
      {instruction?.co != null && <COInstruction {...instruction?.co} />}
      {instruction?.beforeList != null && (
        <PlainTextList list={instruction?.beforeList} />
      )}
      <DisplayOptions
        menu={menu}
        setMenu={setMenu}
        options={["Chart", "Inline (text)", "Split by motif (text)"]}
      />
      {menu === "1" && (
        <>
          <InteractiveAlert isRow={true} />
          <Box width="100%" overflow="auto">
            <ChartComponent
              {...chart}
              focus={focus}
              setFocus={setFocus}
              isOnlyRowClickable={true}
            />
          </Box>
        </>
      )}
      {menu === "2" && <Written {...chart} />}
      {menu === "3" && <Written {...chart} display="block" />}
      {instruction?.afterList != null && (
        <PlainTextList list={instruction?.afterList} />
      )}
    </Flex>
  );
}
