import { Alert, AlertIcon } from "@chakra-ui/react";

interface Props {
  isRow?: boolean;
}

export default function InteractiveAlert({ isRow = false }: Props) {
  return (
    <Alert width="fit-content">
      <AlertIcon />
      Chart is interactive - click a {isRow ? "row" : "stitch group"} to see the
      corresponding written instruction.
    </Alert>
  );
}
